.container {
  width: 100%;
  min-height: 100vh;
  margin: 0;
  padding: 0;
  background: -webkit-gradient(
    linear,
    left bottom, left top,
    from(rgba(21, 21, 21, 0.9)),
    color-stop(30%, rgba(21, 21, 21, 1)),
    color-stop(20%, rgba(21, 21, 21, 1)),
    to(rgba(21, 21, 21, 0.8))
  );
  background: -webkit-linear-gradient(
    bottom,
    rgba(21, 21, 21, 0.9),
    rgba(21, 21, 21, 1) 30%,
    rgba(21, 21, 21, 1) 20%,
    rgba(21, 21, 21, 0.8)
  );
  background: -o-linear-gradient(
    bottom,
    rgba(21, 21, 21, 0.9),
    rgba(21, 21, 21, 1) 30%,
    rgba(21, 21, 21, 1) 20%,
    rgba(21, 21, 21, 0.8)
  );
  background: linear-gradient(
    to top,
    rgba(21, 21, 21, 0.9),
    rgba(21, 21, 21, 1) 30%,
    rgba(21, 21, 21, 1) 20%,
    rgba(21, 21, 21, 0.8)
  );
}



