.movie {
  width: 200px;
  height: 300px;
  position: relative;
  margin: 20px;
  margin-left: 0;

  cursor: pointer;
  -webkit-box-shadow: 0 2px 5px 0 rgba(0, 0, 0, 0.7), 0 2px 10px 0 rgba(0, 0, 0, 0.7);
          box-shadow: 0 2px 5px 0 rgba(0, 0, 0, 0.7), 0 2px 10px 0 rgba(0, 0, 0, 0.7);
  overflow: hidden;
  -webkit-transition: all 0.5s;
  -o-transition: all 0.5s;
  transition: all 0.5s;
}

.movie-img {
  z-index: 1;
  width: 100%;
}

.movie-title {
  display: -ms-flexbox;
  display: flex;
  -ms-flex-pack: center;
      justify-content: center;
  -ms-flex-align: center;
      align-items: center;
  min-height: 100px;
  margin: 0;
  padding: 20px;
  color: #fff;
  font-weight: 700;
  font-size: 16px;
}

.movie-top-overlay {
  position: absolute;
  display: -ms-flexbox;
  display: flex;
  -ms-flex-pack: center;
      justify-content: center;
  -ms-flex-align: center;
      align-items: center;
  height: 100px;
  width: 100%;
  top: -100px;
  -webkit-transition: all 0.5s;
  -o-transition: all 0.5s;
  transition: all 0.5s;
  background: rgba(108, 121, 107, 0.75);
}

.movie-desc {
  display: -ms-flexbox;
  display: flex;
  -ms-flex-direction: column;
      flex-direction: column;
  -ms-flex-align: center;
      align-items: center;
  -ms-flex-wrap: wrap;
      flex-wrap: wrap;
  position: absolute;
  bottom: -100px;
  width: 100%;
  height: 200px;

  background: rgba(54, 84, 99, 0.75);
  -webkit-transition: all 0.5s;
  -o-transition: all 0.5s;
  transition: all 0.5s;
}

.movie:hover .movie-desc {
  bottom: 0;
}

.movie:hover .movie-top-overlay {
  top: 0;
}

.movie-genres {
  width: 100%;
  list-style: none;
  display: -ms-flexbox;
  display: flex;
  -ms-flex-wrap: wrap;
      flex-wrap: wrap;
  margin: 0;
  padding: 0 5px;
  text-align: center;
}

.movie-genres-item {
  margin: 2px 5px;
}

.movie-genres-item > span {
  font-weight: 500;
}



